.today {
  margin-top: 132px;
}

.today__title {
  margin-bottom: 55px;
}

.today__list {
  display: flex;
}

.today__list-item {
  display: flex;
  flex-direction: column;
}

.today__list-item:not(:last-child) {
  margin-right: 20px;
}

.today__list-item:nth-child(even) {
  margin-top: 50px;
}

.today__list-text {
  font-size: 20px;
  font-weight: 400;
  line-height: 22px;
  text-align: center;
  margin-top: 13px;
}